import { AfterViewInit, Component, Input } from '@angular/core';
import { BcSkipLinkBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

@Component({
  selector: 'bc-skip-link',
  templateUrl: './bc-skip-link.component.html',
})

/**
 * Componente Skip-Link
 *
 * @export
 * @class BcSkipLinkComponent
 * @implements {AfterViewInit}
 */
export class BcSkipLinkComponent implements AfterViewInit {

  constructor (private bcHelperService: BcHelperService) {
    this.skipLinkBehavior = new BcSkipLinkBehavior();
  }
    /**
   * Esta entrada permite indicar un id único al componente dentro del DOM.
   *
   * @type {string}
   * @memberof BcSkipLinkComponent
   */
     @Input() id: string = this.bcHelperService.getId(this);
  /**
   * Texto en el label.
   *
   * @type {string}
   * @memberof BcSkipLinkComponent
   */
  @Input('text-link') textLink = 'Ir al contenido principal';

  /**
   * se indica a que id se va a trasladar la página
   *
   * @type {string}
   * @memberof BcSkipLinkComponent
   */
  @Input('refence-id') referenceId = '';

  skipLinkBehavior: BcSkipLinkBehavior;
  ngAfterViewInit(): void {
    this.skipLinkBehavior.setId(this.id);
    this.skipLinkBehavior.setUp();
  }
}